import { useWeb3React } from "@web3-react/core";
import { useEffect, useState } from "react";
import { injected } from "./components/connectors/injected";
import {Abi} from "./contractAbi.json";
import swal from "sweetalert";

function App() {
  
  const contract = "0xAD1E2f52ffC96C09D80E17b446B3b8ee99A699f9";
  
  const raiseWallet = "0x66bc751ceda9CEf57c9DbB6691A88DB46529EcFd";

  const hardCap = 7000;

  const rate = 120000000000;

  const [raisedAmount, setRaisedAmount] = useState(0);

  const [tkAmount, setTkAmount] = useState(0);

  const [currency, setCurrency] = useState("N/A");

  const [bnbvalue, setBNBValue] = useState(0);

  const [crowdsale, setCrowdsale] = useState({});

  const { active, account, library, connector, activate, deactivate, chainId} = useWeb3React();
  const [error, setError] = useState("");
  const [balance, setBalance] = useState(0);
  const [value, setValue] = useState(0);

  async function connect(){
    try {
      await activate(injected);
    } catch (error) {
      console.log('error', error)
    }
  }

  async function setCrowdsaleMethod(){
    if(active){
      const crowdsale = await new library.eth.Contract(Abi, contract);
      
      var raiseBlnc = await library.utils.fromWei(await crowdsale.methods.weiRaised().call(), "ether");

      (raiseBlnc!=0) ? setRaisedAmount(parseFloat(raiseBlnc).toFixed(3)):setRaisedAmount(raiseBlnc);

      setCrowdsale(crowdsale);
    }
  }

  async function disconnect(){
    try {
      await deactivate();
    } catch (error) {
      console.log('error', error)
    }
  }

  async function setBlnc(){
    if(active){
      let blnc = await library.eth.getBalance(account);

      blnc = await library.utils.fromWei(blnc, 'ether');
      if(blnc != 0){
        setBalance(parseFloat(blnc).toFixed(3));
      } else {
        setBalance(blnc);
      }
    }
  }

  function setCurrencyMethod(){
    if(chainId){
      if(chainId == 1){
        setCurrency("ETH");
      } else if(chainId == 97 || chainId == 56){
        setCurrency("BNB");
      }
    }
  }

  async function sendTransaction(){
    try {

      // if(bnbvalue < 0.02|| bnbvalue > 10){
      //   setError("BNB must be between 0.1 and 10");
      //   return;
      // }

      if(!active) {setError("Connect to the wallet."); return;}

      // if(chainId!== 97){setError("Only for test network at this time. ðŸ˜Š"); return;}
            
      let val = await library.utils.toWei(bnbvalue, "ether");

      console.log('val', val)

      try {
        await library.eth.sendTransaction({from: account, to: contract, value: val});
      } catch (error) {
        swal("", error.message, 'error');
      }

    } catch (error) {
      console.log("error", error)
    }
  }

  function calculateToken(){
    let tkamount = bnbvalue * rate;
    setTkAmount(tkamount);
  }

  async function withdraw(){
    try {
      await crowdsale.methods.withdraw().call().then((result) => {
        console.log(result);
      })
    } catch (error) {
      swal('', error.message, 'error');
    }
  }

  useEffect(()=>{
    setBlnc();
    setCurrencyMethod();
    calculateToken();
    setCrowdsaleMethod();
  },[account, chainId, bnbvalue])

  return (
    <div className="container">
      <div className="row">
        <div className="col-md-12">
          <div className="card mt-2 col-centered">
            <div className="card-body">
              <div className="row">
                <div className="col-sm-6">
                  <div className="logo-area">
                      <img src="logo.png" className="logo img-responsive"/> <strong className="logo-text">Genesis Coin Presale</strong>
                  </div>
                </div>
                <div className="col-sm-6">
                  {
                    active
                      ?
                        <div>
                          <span className="account float-right">

                              <span className="balance">{`${balance} ${currency}`}</span>
                            <a className="white" href={`https://testnet.bscscan.com/address/${account}`} target="new">
                              {`${account.substring(0,6)}.....${account.substring(38)}`}
                            </a>
                          </span>
                        </div>
                      :
                        <button onClick={connect} className="connect-btn btn btn-sm btn-primary float-right">Connect</button>
                  }
                </div>
              </div>
            </div>

              {
                account 
                  ?
                    <div className="card-body form-body">
                      <form>
                        <div className="form-group">
                          <span className="raise-amount float-right">(Raised: {`${raisedAmount} / ${hardCap} ${currency}`})</span>
                          <label>BNB Amount:</label>
                          <input value={bnbvalue} onInput={e => setBNBValue(e.target.value)} className="form-control" type="number" id="bnb" placeholder="Enter your BNB contribution"/>
                          {
                            tkAmount ? <span>{tkAmount}</span>:''
                          }
                        </div>
                        <button type="button" className="send btn btn-primary btn-sm" onClick={sendTransaction}>Send</button>
                      
                      </form>
                     
                    </div>
                  :''
              }
              <div className="card-header">

                <div className="row">
                  <div className="col-sm-6 contract-heading">
                      Contract Details  
                  </div>
                  <div className="col-sm-6 contract-heading">
                      <span className="float-right">
                      <button className="btn btn-sm btn-danger" onClick={withdraw}>Withdraw <span className="badge badge-warning">5 BNB</span></button>
                      </span>
                  </div>
                </div>

              </div>
              <ul className="list-group list-group-flush">
                <li className="list-group-item">Total Supply: 1000,000,000,000,000</li>
                <li className="list-group-item">Tokens For Presale: 300,000,000,000,000</li>
                <li className="list-group-item">Token For Liquidity: 200,000,000,000,000</li>
                <li className="list-group-item">Soft Cap: 100 BNB</li>
                <li className="list-group-item">Hard Cap: 7000 BNB</li>
                <li className="list-group-item">Presale Rate: 100,000,000,000</li>
                <li className="list-group-item">Pancakeswap Listing Rate: 90,000,000,000</li>
                <li className="list-group-item"></li>
                <li className="list-group-item"></li>
                <li className="list-group-item"></li>
                <li className="list-group-item"></li>
                {/* <li class="list-group-item"></li> */}
              </ul>
          </div>
        </div>  
      </div>
    </div>
  );
}

export default App;
